<template>
  <v-app>
    <v-container>
    <v-data-table
      :headers="headers"
      :items="queues"
      sort-by="_id"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="grey lighten-4">
          <v-toolbar-title>Monitored Queues</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >Add new Queue</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem._id" label="Queue ID"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.queue" label="Queue GUID"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.name" label="Name"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field v-model="editedItem.enabled" label="Enabled"></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:item.status="{ item }">
        <v-switch v-model="item.enabled" class="ma-2" label=""
        :input-value=item.enabled
        @change="toggleItem(item.enabled, item._id)"
        >
        mdi-dense
        </v-switch>
      </template>

      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
    </v-container>
  </v-app>
</template>


// import HelloWorld from './components/HelloWorld';
<script>
  export default {
    data: () => ({
      dialog: false,
      headers: [
        {
          text: 'Queue ID',
          align: 'start',
          sortable: false,
          value: '_id',
        },
        { text: 'GUID', value: 'queue' },
        { text: 'Name', value: 'name' },
        { text: 'Enabled', value:'status', sortable: false },
        { text: 'Actions', value: 'actions', sortable: false },
      ],
      queues: [],
      editedIndex: -1,
      editedItem: {
        _id: '',
        queue: '',
        name: '',
        enabled: false,
      },
      defaultItem: {
         _id: '',
        queue: '',
        name: '',
        enabled: false,
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Add new queue for monitoring' : 'Edit queue'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    created () {
      this.initialize()
    },
    methods: {
      initialize () {
        this.queues = [
          {
              queue: "7986b8f4-6035-4280-8adf-40244ce4ba10",
              name: "testi3",
              enabled: true,
              _id: "JCpK6TAoPXBR5jR6"
          },
          {
              queue: "7986b8f3-6035-4280-8adf-40244ce4ba10",
              name: "testi2",
              enabled: true,
              _id: "NuDZNukzbv8k8mpo"
          },
          {
              queue: "7986b8f5-6035-4280-8adf-40244ce4ba10",
              name: "testi5",
              enabled: true,
              _id: "TCDfzBtf4yfZNHZg"
          },
          {
              queue: "7986b8f2-6035-4280-8adf-40244ce4ba10",
              name: "testi1",
              enabled: false,
              _id: "XCayEJ5G0bNez94i"
          }
        ]
      },
      toggleItem (enabled, _id){
        console.log(enabled, _id)
        const index = this.queues.findIndex(x => x._id === _id)
        console.log(index)
        console.log(this.queues[index].enabled)
        
      },
      editItem (item) {
        this.editedIndex = this.queues.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        const index = this.queues.indexOf(item)
        confirm('Are you sure you want to delete this queue?') && this.queues.splice(index, 1)
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.queues[this.editedIndex], this.editedItem)
        } else {
          this.queues.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>